<script>
	import NavigationItem from "@/components/Cabinet/NavigationItem";
	import { mapState, mapMutations } from "vuex";

	export default {
		name: "NavigationPanel",
		components: { NavigationItem },
		data: () => ({
			tabs: [
				{ id: 1, selected: false, tab: "appeals", icon_fill: "" },
				{ id: 2, selected: false, tab: "parsers", icon_fill: "" },
				{ id: 3, selected: false, tab: "favorites", icon_fill: "" },
				{ id: 4, selected: false, tab: "profile", icon_fill: "" },
			],
		}),
		watch: {
			tab() {
				this.paint_tab();
			},
		},
		computed: {
			...mapState({ tab: (state) => state.navigation_panel.tab }),
		},
		methods: {
			...mapMutations(["SET_TAB"]),
			set_tab(tab) {
				this.SET_TAB(tab);
				this.$router.push({ name: tab, query: { page: 1 } });
			},
			paint_tab() {
				this.tabs.forEach((tab) => {
					tab.tab === this.tab
						? ((tab.selected = true), (tab.icon_fill = "selected"))
						: ((tab.selected = false), (tab.icon_fill = ""));
				});
			},
			paint_icon() {
				for (let [key, value] of Object.entries(this.$refs)) {
					console.log(key, value);
				}
			},
		},
		created() {
			this.paint_tab();
		},
	};
</script>

